import { createWebHistory, createRouter } from "vue-router";
// 컴포넌트 임포트.
import List from './components/List.vue'
import Home from './components/Home.vue'
import Detail from './components/Detail.vue'
import Author from './components/Author.vue'
import Comment from './components/Comment.vue'

const routes = [
    {
        path: "/",  // URL
        component: Home,  // 임포트해온 컴포넌트
    },
    {
        path: "/list",  // URL
        component: List,  // 임포트해온 컴포넌트
    },
    {
        // URL/:any, 
        // URL/:any(\\d+)정규식 입력가능(숫자만 입력)
        // URL/:any* 은 /URL/:any/:any... 반복된 패턴 처리.
        path: "/detail/:id",  
        component: Detail,  // 임포트해온 컴포넌트
        beforeEnter: (to, from)=> {   // Navigation guards
            console.log("로그인 체크!!!", to.fullPath, to.params.id, from)
        },
        children: [
            {
                path: "author",
                component: Author,
            },
            {
                path: "comment",
                component: Comment,
            }
        ]
    },
    {
        path: "/:any",  // 404 처리. 또는 :any(.*) 정규식 적용으로 더 엄격함.
        component: Home,  // 404 시 Home 연결
    }
];

const router = createRouter({
  history: createWebHistory(), // 이게 문제가 되는 경우, history: createWebHasHistory() Hash mode 사용.
  routes,
});

// 여러개의 route에 같은 navigation guard 추가
router.beforeEach((to, from) => {
    console.log('each router : ',to, from);
})

export default router; 